<template>
  <div>
    <main> <!-- Début du main -->
      <div class="title"> <!-- title de présentation, dans le main -->
        <h1>
          <BarLoader style="margin: 0 auto; border-radius:5px" v-if="artworkLoading"></BarLoader>
          <StateDisplay :state="getArtwork.state"/>
          {{getArtwork.name}}
          <span v-if="getArtwork.date" class="date">({{getArtwork.date}})</span>
        </h1>
        <span class="actions" v-if="jwt">
          <img src="@/assets/icon/edit.svg" class="action" @click="$router.push('/'+getPerson.slug+'/'+getArtwork.slug+'/edit')">
        </span>
      </div>
      <input v-if="!artworkLoading" class="print" type="button" value="Imprimer cette fiche" @click="$router.push('/'+getPerson.slug+'/'+getArtwork.slug+'/print')" />
      <div class="card content" v-if="artworkLoading">
        <BarLoader style="margin: 0 auto; border-radius:5px"></BarLoader>
      </div>
      <div class="card content" v-else>
        <viewer :value="getArtwork.content"/>
      </div>
      <div class="card text" v-if="getArtwork.text">
        <h2>Texte</h2>
        <BarLoader style="margin: 0 auto; border-radius:5px" v-if="artworkLoading"></BarLoader>
        <viewer :value="getArtwork.text" />
      </div>
      <div class="card notes" v-if="getArtwork.notes">
        <h2>Notes</h2>
        <BarLoader style="margin: 0 auto; border-radius:5px" v-if="artworkLoading"></BarLoader>
        <viewer :value="getArtwork.notes" />
      </div>
    </main><!-- fin main -->
  </div>
</template>
<script>
// @ is an alias to /src
import { mapActions, mapGetters } from 'vuex'
// import 'tui-editor/dist/tui-editor-contents.css';
// import 'highlight.js/styles/github.css';
import Viewer from '@toast-ui/vue-editor/src/Viewer.vue'
import StateDisplay from '@/components/state/Display.vue'


export default {
  name: 'home',
  components: {
    Viewer,
    StateDisplay
  },
  data () {
    return {
        editable: false
    }
  },
  // Fetches posts when the component is created.
  created () {
    window.scrollTo(0, 0)
    this.fetchArtwork(this.$route.params.artwork)
    .then(()=>{
      this.fetchPerson(this.getArtwork.person)
    })
  },
  computed: {
        ...mapGetters('jwt',[
        'jwt',
        'jwtSubject',
        'jwtIssuer'
        ]),
        ...mapGetters('artworks',[
        'getArtwork',
        'artworkLoading'
        ]),
        ...mapGetters('persons',[
        'getPerson'
        ])
  },
  methods: {
    ...mapActions('artworks',[
      'fetchArtwork',
    ]),
    ...mapActions('persons',[
      'fetchPerson',
    ]),
    print() {
      window.print()
    }
  }
}
</script>
